<template>
  <div class="home">
    <div>
      <h1 class="p-3 pb-2 fs-5 home-title">LibrerÃ­a</h1>
      <div class="container-books">
        <div class="book" @click="goToWhatsapp(book.title)" v-for="book in books" :key="book.img">
          <div class="img-book">
            <img :src="book.img" alt="">
          </div>

          <span class="pt-2 fw-bold">{{ book.title }}</span>
          <span>${{ formatNumer(book.price) }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import app from '@/firebase.js'
import { getDatabase, ref, onValue } from "firebase/database";

export default {
  name: 'Home',
  components: { },
  data() {
    return {
      publication: '',
      books: [],
      db: null
    }
  },
  async mounted() {
    //const db = firebase.database().ref();
    this.db = getDatabase();
    const starCountRef = ref(this.db, 'books/');
    onValue(starCountRef, (snapshot) => {
      const data = snapshot.val()
      console.log("data: ", data)
      this.books = data
    })

    console.log(getDatabase(app))
  },

  methods: {
    formatNumer(number) {
      const formatSpain = new Intl.NumberFormat('es-ES', { style: 'decimal' })

      return formatSpain.format(number);
    },

    getTextLink(message) {
      const codedMessage = encodeURIComponent(message);
      return codedMessage
    },

    goToWhatsapp(bookName) {
      const message = this.getTextLink("Me interesa el libro " + bookName)
      window.location.href = 'https://wa.me/' + "57" + "3214020472" + "?text=" + message
    }
  }
}
</script>

